(function() {
  'use strict';

  angular
    .module('englishGalore')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, $animateProvider, $locationProvider, $provide, $authProvider, apiUrl) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Animate only elements with class "animate"
    $animateProvider.classNameFilter(/animate/);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = false;
    toastrConfig.progressBar = false;

    //Removes '#' from the URL
    $locationProvider.html5Mode(true);
    $locationProvider.hashPrefix('!');

    $authProvider.configure({
      apiUrl:                  apiUrl,
      tokenValidationPath:     '/v1/auth/validate_token',
      signOutUrl:              '/v1/auth/logout',
      emailRegistrationPath:   '/v1/auth/register',
      accountDeletePath:       '/v1/auth/delete_account',
      passwordResetPath:       '/v1/auth/password',
      passwordUpdatePath:      '/v1/auth/password',
      passwordResetSuccessUrl: window.location.origin, //eslint-disable-line
      emailSignInPath:         '/v1/auth/login'
    });



    //Used for ckeditor
    $provide.decorator('taOptions', ['taRegisterTool', '$delegate', function(taRegisterTool, taOptions) { // $delegate is the taOptions we are decorating

        taOptions.toolbar = [
            ['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p', 'quote','bold', 'italics','underline'],
            ['strikeThrough','ul', 'ol','justifyLeft', 'justifyCenter', 'justifyRight', 'indent', 'outdent', 'insertLink']
        ];
        return taOptions;
    }]);
  }

})();

